import MyTimePicker from '@/components/myTimePicker/MyTimePicker.vue'
import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	components: {
		MyTimePicker,
		wPicker
	},
	data() {
		return {
			type: '羽毛球',
			time: '',  //选择的时间值
			isshow1: false,  //显示时间选择器
			isshow2: false,  //其他选择器
			hour: null,  //选择的时长
			pickList1: [],
			deadline: '',  //报名截止
			timePickerType: 100,  //100为选择活动时间，200为选择报名截止时间
			inputNumValue: '',  //人数
			inputPrice1: '',  //钱包支付费用
			inputPrice2: '',  //费用男
			inputPrice3: '',  //费用女
			inputPrice4: '',  //临打费用
			inputBallType: '',  //活动用球
			columns: [],
			optionsList: [],
			selectedIds: [],  //选中的等级
			inputOrganizer: '',  //组织者
			titleValue: '',  //标题
			ruleValue: '',  //活动规则
			isWeek: false,  //是否周活动
			isCancel: false,  //是否可取消
		}
	},
	onLoad() {
		this.columns = [{label:"0.5小时",value:"0.5"},{label:"1小时",value:"1"},{label:"1.5小时",value:"1.5"},{label:"2小时",value:"2"},{label:"2.5小时",value:"2.5"},{label:"3小时",value:"3"},{label:"3.5小时",value:"3.5"},{label:"4小时",value:"4"}];
		this.optionsList = [{id: 1, value: '菜鸟'},{id: 2, value: '初级'},{id: 3, value: '中级'},{id: 4, value: '高级'}]
	},
	computed: {
		isShowButton() {
			if(this.isshow1) {
				return !this.isshow1
			}if(this.isshow2) {
				return !this.isshow2
			}else{
				// setTimeout(function() {
					return true
				// }, 200);
			}
		}
	},
	methods: {
		// 选择活动时间
		choiceTime() {
			this.isshow1 = !this.isshow1
			this.timePickerType = 100
		},
		receiveTime(v1, v2){
			this.time = v1
			this.isshow1 = v2
		},
		// 选择活动时长
		choiceDuration() {
			this.isshow2 = true
			this.$refs.picker1.show()
		},
		onConfirm(v) {
			this.hour = v.checkArr.value
			this.isshow2 = false
		},
		onCancel() {
			this.isshow2 = false
		},
		// 选择报名截止时间
		choiceDeadline() {
			this.isshow1 = true
			this.timePickerType = 200
		},
		receiveDeadline(v1, v2){
			this.deadline = v1
			this.isshow1 = v2
		},
		// 选择等级
		handleClick(item) {
			let selectedIndex = this.selectedIds.indexOf(item.id)
			 if(selectedIndex >= 0) {
				this.selectedIds.splice(selectedIndex, 1)
			}else{
				this.selectedIds.push(item.id)
			}
			// console.log(this.selectedIds)
		},
		// 输入人数
		inputNum(v) {
			this.inputNumValue = v.detail
		},
		// 输入钱包支付
		InputPrice1(v) {
			this.inputPrice1 = v.detail
		},
		// 输入费用男
		InputPrice2(v) {
			this.inputPrice2 = v.detail
		},
		// 输入人数
		InputPrice3(v) {
			this.inputPrice3 = v.detail
		},
		// 输入人数
		InputPrice4(v) {
			this.inputPrice4 = v.detail
		},
		// 输入标题
		inputTitleValue(v) {
			this.titleValue = v.detail
		},
		// 输入规则
		inputRuleValue(v) {
			this.ruleValue = v.detail
		},
		// 输入用球
		InputBall(v) {
			this.inputBallType = v.detail
		},
		// 输入组织者
		InputOrganizer(v) {
			this.inputOrganizer = v.detail
		},
		// 是否周活动
		switchChange1() {
			this.isWeek = !this.isWeek
		},// 是否可取消
		switchChange1() {
			this.isCancel = !this.isCancel
		},
		// 提交
		submit() {
			const params = {
				type: '001',
				time: this.time,
				duration: this.hour,
				deadline: this.deadline,
				place: '',
				people: this.inputNumValue,
				money: this.inputPrice1,
				moneyMan: this.inputPrice2,
				moneyWomen: this.inputPrice3,
				temporaryMoney: this.inputPrice4,
				ballType: this.inputBallType,
				level: this.selectedIds,
				title: this.titleValue,
				rule: this.ruleValue,
				isWeek: this.isWeek,
				isCancel: this.isCancel
			}
			console.log(params)
		}
	}
}
